body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.description-name {
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.description-date {
  color: black;
  font-weight: bold;
}

.loop {
  position: absolute;
  z-index: 10000000000;
  top: 12%;
  display: flex;
  left: 0.5%;
}

.buttonSearch {
  background-image: url("../public/search.png");
  background-size: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: none;
}

.inputSearch {
  height: 20px;
  margin-left: 5%;
}

.marker-inf {
  position: absolute;
  z-index: 10000000000;
  display: block;
  pointer-events: visiblePainted;
  background: white;
  width: 20%;
  height: auto;
  border: 2px solid black;
  border-radius: 10px;
  right: 1%;
  top: 1%;
  text-align: center;
}
@media screen and (max-width: 450px) {
  .marker-inf {
    width: 45%;
  }
}
.marker-inf .markerName {
  text-align: center;
  width: 100%;
  font-weight: bold;
  font-size: 14px;
}
.marker-inf .date {
  width: 100%;
  text-align: center;
  display: block;
}
.marker-inf > p {
  font-size: 12px;
  padding: 2px;
}
@media screen and (max-width: 450px) {
  .marker-inf > p {
    max-height: 70px;
    overflow: scroll;
    overflow-x: hidden;
  }
}
.marker-inf > a {
  font-size: 14px;
}
.marker-inf > button {
  margin-top: 3%;
  margin-bottom: 3%;
}

#youtubeVideo {
  position: absolute;
  z-index: 10000000000;
  width: 70%;
  height: 45vh;
  top: 35%;
  left: 15%;
}
#youtubeVideo > iframe {
  width: 100%;
  height: 100%;
}
#youtubeVideo .close {
  position: absolute;
  top: -3%;
  right: -2%;
}
#youtubeVideo .close > button {
  border-radius: 50%;
  font-size: 23px;
  cursor: pointer;
}

.loading {
  position: absolute;
  z-index: 10000000000;
  top: 45%;
  left: 45%;
  width: 10%;
  height: 10%;
}
@media screen and (max-width: 480px) {
  .loading {
    width: 20%;
    height: 20%;
  }
}
.loading > img {
  width: 100%;
  height: 100%;
}

.search {
  position: absolute;
  z-index: 10000000000;
  top: 13%;
  left: 0.4%;
}
@media screen and (max-width: 400px) {
  .search {
    left: 2.2%;
    top: 15%;
  }
}

.search-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0%, -50%);
  background: #ccc;
  height: 30px;
  border-radius: 40px;
  padding: 5px;
  display: flex;
}

.search-btn {
  color: black;
  float: right;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: 0.4s;
}

.search-txt {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: black;
  font-size: 16px;
  transition: 0.4s;
  line-height: 40px;
  width: 0px;
}

.search-box:hover > .search-txt {
  width: 240px;
  padding: 0 6px;
}
@media screen and (max-width: 480px) {
  .search-box:hover > .search-txt {
    width: 150px;
  }
}

.search-box:hover > .search-btn {
  background: white;
}/*# sourceMappingURL=index.css.map */